.edu__container {
    display: block;
    padding-left: 3%;
}

.edu__card {
    padding: 1rem;
    display: grid;
    grid-template-columns: 30% 50%;   
}

.field {
    display: grid;
    grid-template-columns: 40% 35% 20%;
    padding-left: 5rem;
}

.courses {
    display: grid;
    grid-template-columns: 40% 60%;
    padding-left: 2rem;
}

.courses h3 {
    font-size: 1rem;
}

.edu_img {
    width: 12rem;
    margin-left: 1.5%;
}

.edu_text {
    margin-left: 3rem;
    text-align: center;    
}

.edu_text h1 {
    color: var(--color-primary);
    font-size: 3rem;
}

.edu_text h4 {
    font-size: 1rem;
    font-weight: 350;
}

.edu_text:hover {
    background: transparent;
    border-color: var(--color-bg-variant);
    cursor: default;
}

/******************** Mediun Devices *******************/
@media screen and (max-width: 1024px) {
    .edu__container {
        display:inline;
    }


    .cu_img{
        padding-top: 0rem;
    }
    
    
    .emu_img {
        padding-top: 0rem;
    }
    
    .ikiu_img {
        padding-top: 0rem;
    }
}